'use client'

import {useState} from 'react'
import * as R from 'remeda'
import {Delimiter, EMPTY_OBJECT, NOT_APPLICABLE} from '@nyx/kr-common/constant/Constant'
import {Base62, Url} from 'src/kr/common/util/Encoder'
import {Base64} from 'js-base64'

export default function KrEncoder() {
    const [input, setInput] = useState<string>(Delimiter.EMPTY)
    const [encoded, setEncoded] = useState<Record<string, string>>(EMPTY_OBJECT)
    const [decoded, setDecoded] = useState<Record<string, string>>(EMPTY_OBJECT)

    // encode
    const handleEncodeButtonClick = () => {
        setEncoded({
            urlEncoded: Url.encode(input),
            base62Encoded: Base62.encode(input),
            base64Encoded: Base64.encode(input),
            base64UrlEncoded: Base64.encodeURI(input)

        })
        setDecoded(EMPTY_OBJECT)
    }

    // decode
    const handleDecodeButtonClick = () => {
        setDecoded({
            urlDecoded: Url.decode(input),
            base62Decoded: Base62.decode(input),
            base64Decoded: Base64.decode(input),
            base64UrlDecoded: Base64.decode(input)

        })
        setEncoded(EMPTY_OBJECT)
    }

    return (
        <div style={{padding: "20px", margin: "0 auto"}}>
            <h1>Encoder/Decoder (URL, Base62, Base64)</h1>
            <textarea
                value={input}
                onChange={(e) => setInput(e.target.value.trim())}
                rows={4}
                placeholder="Enter text to encode or decode"
                style={{width: "100%", padding: "10px", marginBottom: "10px"}}
            />
            <div>
                <button onClick={handleEncodeButtonClick} style={{margin: "10px"}}>Encode</button>
                <button onClick={handleDecodeButtonClick}>Decode</button>
            </div>
            {/* Encoding */}
            {!R.isEmpty(encoded) && (
                <div style={{marginTop: "20px"}}>
                    <h2>Encoding Result</h2>
                    <pre style={{backgroundColor: "#f0f0f0", padding: "10px"}}>
                        <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>URL Encoded:</strong>{encoded.urlEncoded}
                        <br/><br/>
                        <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>Base62 Encoded:</strong>{encoded.base62Encoded}
                        <br/><br/>
                        <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>Base64 Encoded:</strong>{encoded.base64Encoded}
                        <br/><br/>
                        <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>Base64 URL-Safe Encoded:</strong>{encoded.base64UrlEncoded}
                    </pre>
                </div>
            )}
            {/* Decoding */}
            {!R.isEmpty(decoded) && (
                <div style={{marginTop: "20px"}}>
                    <h2>Decoding Result</h2>
                    <pre style={{backgroundColor: "#f0f0f0", padding: "10px"}}>
                        <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>URL Decoded:</strong>{R.isEmpty(decoded.urlDecoded) ? NOT_APPLICABLE : decoded.urlDecoded}
                        <br/><br/>
                        <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>Base62 Decoded:</strong>{R.isEmpty(decoded.base62Decoded) ? NOT_APPLICABLE : decoded.base62Decoded}
                        <br/><br/>
                        <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>Base64 Decoded:</strong>{R.isEmpty(decoded.base64Decoded) ? NOT_APPLICABLE : decoded.base64Decoded}
                        <br/><br/>
                        <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>Base64 URL-Safe Decoded:</strong>{R.isEmpty(decoded.base64UrlDecoded) ? NOT_APPLICABLE : decoded.base64UrlDecoded}
                    </pre>
                </div>
            )}
        </div>
    )
}